(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osHeader', header);

    /** @ngInject */
    function header() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/header/header.html',
            scope: {},
            controller: HeaderController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function HeaderController(osApi, osAuth, $stateParams, $state, $timeout, $rootScope) {


            osApi.query("lookup_oncoscape_tools",{beta:false}).then(function(response){
                vm.tools = response.data;
                
            });

            osApi.onDataSource.add(function(){                
                $timeout(function(){
                    vm.datasets = osApi.getDataSources();
                    vm.showTools = true;
                });
            });

            
           
            var vm = this;
            //vm.showMenu = false;
            //vm.showTools = false;

            vm.showMenu = false;
            vm.showTools = false;

            var currentTool;
            $rootScope.$on('$stateChangeStart', function(event, toState, toParams, fromState, fromParams){ 
                currentTool = toState.name;
                switch(toState.name){
                    case "landing":
                    case "tools":
                    case "datasource":
                        vm.showMenu = false;
                        break;
                    default:
                        vm.showMenu= true;
                        vm.showTools= true;
                        break;
                }
            });

            vm.loadDataset = function(dataset) {
                $state.go(currentTool, {
                    datasource: dataset
                });
            };

            vm.loadTool = function(tool) {
                $state.go(tool, {
                    datasource: osApi.getDataSource().disease
                });
            };
            
            vm.logoutClick = function(){
               osAuth.logout();
               $state.transitionTo("landing");
            }
        }
    }

})();
(function() {
    'use strict';
    
    angular
        .module('oncoscape')
        .directive('osGenesettest', genesettest);

    /** @ngInject */
    function genesettest() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/genesettest/genesettest.html',
            controller: GenesettestController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function GenesettestController(osApi, osHistory, $state, $stateParams, $timeout, $scope, d3, $window, _) {

            if (angular.isUndefined($stateParams.datasource)) {
                $state.go("datasource");
                return;
            }

            // Elements
            var elInput = angular.element("#geneSetTestsInputsDiv");
            
            // Properties
            //var cohortPatient = osApi.getCohortPatient();

            // View Model
            var vm = this;
            vm.datasource = $stateParams.datasource;
            vm.geneSets = [];
            vm.geneSet = null;

        
            var cohort1 = osHistory.getPatientSelections()[0];
            var cohort2 = osHistory.getPatientSelections()[1];

            // Initialize
            osApi.setBusy(true);
            osApi.setDataset(vm.datasource).then(function(response) {
                var mtx = response.payload.rownames.filter(function(v) {
                        return v.indexOf("mtx.mrna") >= 0
                    });

                mtx = mtx[mtx.length - 1].replace(".RData", "");
                console.log("**** mtx is: ", mtx);

                if(cohort1 == null || cohort2 == null){
                    vm.message = "Please select two cohorts to test out the Gene Set";
                    vm.optCohort1 = "Empty";
                    vm.optCohort2 = "Empty";
                }else{
                    vm.optCohort1 = cohort1.tool + " " +cohort1.desc + " " + cohort1.ids.length + " Patients selected" ;
                    vm.optCohort2 = cohort2.tool + " " +cohort2.desc + " " + cohort2.ids.length + " Patients selected" ;
                    var geneset = "random.24";
                    //var geneset = "tcga.pancan.mutated";
                    osApi.getGeneSetTest(vm.datasource, mtx).then(function() {
                        $scope.$watchGroup(['vm.optCohort1', 'vm.optCohort2'], function() {
                           calculateGeneSetScore(cohort1, cohort2, geneset);
                        });  
                     });   
                }
                osApi.setBusy(false);
            });

            var drawHeatMap = function(pt, genes, expressionData){
                console.log(expressionData);
                

                var zValues = JSON.parse(expressionData);
                var colorscaleValue = [
                      [1, '#339966'],
                      [2, '#003333']
                    ];

                    var data = [{
                      //x: genes,
                      y: pt,
                      z: zValues,
                      type: 'heatmap',
                      //colorscale: colorscaleValue,
                      showscale: true
                    }];

                    var layout = {
                      title: 'GeneSet Heatmap',
                      annotations: [],
                      xaxis: {
                        ticks: '',
                        side: 'top'
                      },
                      yaxis: {
                        ticks: '',
                        ticksuffix: ' ',
                        width: 500,
                        height: 250,
                        autosize: false
                      }
                    };


                    Plotly.newPlot('heatMap', data, layout);
            }
            var drawHeatMap2 = function(pt, genes, expressionData){
                   console.log(expressionData);
            
                   angular.element('#heatMap').highcharts({

                        chart: {
                            type: 'heatmap',
                            marginTop: 40,
                            marginBottom: 80,
                            plotBorderWidth: 1,
                        },


                        title: {
                            text: 'Gene Set Expression Heat Map'
                        },

                        xAxis: {
                            // labels: {
                            //     step: 1
                            // },
                            categories: genes
                        },

                        yAxis: {
                            lineWidth: 5,
                            lineColor: '#F33',
                            categories: pt,
                            title: null
                        },

                        colorAxis: {
                            min: 0,
                            //minColor:Highcharts.getOptions().colors[7],
                            minColor: "#FFFFFF",
                            maxColor:'#CC9933'
                        },

                        legend: {
                            align: 'right',
                            layout: 'vertical',
                            margin: 10,
                            verticalAlign: 'top',
                            y: 25,
                            symbolHeight: 280
                        },

                        tooltip: {
                            formatter: function () {
                                return '<b>' +this.series.yAxis.categories[this.point.y]  + ' '+ 
                                     this.series.xAxis.categories[this.point.x]+ ': ' +this.point.value + '</b>';
                            }
                        },

                        series: [{
                                    name: 'Sales per employee',
                                    borderWidth: 0,
                                    data: JSON.parse(expressionData) }]
                    });

            }    
            // API Call To oncoprint_data_selection
            var calculateGeneSetScore = function(cohort1, cohort2, geneset) {    
                var Group1 = cohort1.ids;
                var Group2 = cohort2.ids;

                osApi.setBusy(true);
                osApi.getGeneSetScore(Group1, Group2, geneset).then(function(response){
                    console.log(response.payload);
                    if(response.status == "error"){
                        vm.message = response.payload + "Please select two cohorts to test out the Gene Set";
                    }else{
                        vm.message = response.payload.summary;
                        var pt = response.payload.pt;
                        var g = response.payload.genes;
                        drawHeatMap2(pt, g, response.payload.analysisData);
                    }
                    osApi.setBusy(false);
                });
             }    
     }
    } 
})();   
(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osDatasource', datasource);

    /** @ngInject */
    function datasource() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/datasource/datasource.html',
            controller: DatasourceController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function DatasourceController(osApi, $state) {
            var vm = this;
            vm.datasets = osApi.getDataSources();
            vm.explore = function(tool, datasource) {
                osApi.setDataSource(datasource);
                $state.go(tool, {
                    datasource: datasource.disease
                });
            };

        }
    }
})();

(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osDataExplorer', explore);

    /** @ngInject */
    function explore() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/dataexplorer/dataexplorer.html',
            controller: DataExplorerController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function DataExplorerController(osApi, $state, $timeout, $scope, $stateParams, _) {

            if (angular.isUndefined($stateParams.datasource)) {
                $state.go("datasource");
                return;
            }
            // View Model
            var vm = this;
            vm.dataSources;
            vm.dataSource;
            vm.dataDiseases;
            vm.dataDisease;
            vm.dataTables;
            vm.dataTable;
            vm.dataFields;
            vm.dataField;

            var bar = {
                chart: {
                    type: 'discreteBarChart',
                    height: 450,
                    margin: {
                        top: 20,
                        right: 20,
                        bottom: 50,
                        left: 55
                    },
                    x: function(d) {
                        return d.label;
                    },
                    y: function(d) {
                        return d.value;
                    },
                    showValues: true,
                    valueFormat: function(d) {
                        return d3.format(',.4f')(d);
                    },
                    duration: 500,
                    xAxis: {
                        axisLabel: 'X Axis'
                    },
                    yAxis: {
                        axisLabel: 'Y Axis',
                        axisLabelDistance: -10
                    }
                }
            };

            var pie = {
                chart: {
                    type: 'pieChart',
                    height: 450,
                    donut: false,
                    x: function(d) {
                        return d.label;
                    },
                    y: function(d) {
                        return d.value;
                    },
                    showLabels: false,
                    pie: {
                        startAngle: function(d) {
                            return d.startAngle / 2 - Math.PI / 2
                        },
                        endAngle: function(d) {
                            return d.endAngle / 2 - Math.PI / 2
                        }
                    },
                    duration: 500
                }
            };

            vm.chartOptions = bar;

            vm.toolTipContentFunction = function() {
                return function(key, x, y, e, graph) {
                    return 'Super New Tooltip' +
                        '<h1>' + key + '</h1>' +
                        '<p>' + y + ' at ' + x + '</p>'
                }
            }



            vm.xFunction = function() {
                return function(d) {
                    return d.label;
                };
            }

            vm.yFunction = function() {
                return function(d) {
                    return d.value;
                };
            }



            // Load Datasets
            osApi.setBusy(true);
            osApi.query("_collections").then(function(result) {

                vm.dataSources = result.data;
                vm.dataSource = vm.dataSources[0];

                $scope.$watch("vm.dataSource", function() {
                    vm.dataDiseases = vm.dataSource.diseases;
                    vm.dataDisease = vm.dataSource.diseases[0];

                });
                $scope.$watch("vm.dataDisease", function() {
                    vm.dataTables = vm.dataDisease.tables;
                    vm.dataTable = vm.dataTables[0];
                });
                $scope.$watch("vm.dataTable", function() {
                    osApi.query("_field_detail", {
                        collection: vm.dataTable.collection
                    }).then(function(result) {
                        vm.dataFields = result.data[0].fields.filter(function(field) {
                            return field.key != "_id";
                        });
                        vm.dataField = vm.dataFields[0];
                        console.dir(vm.dataField)
                    });
                });

                osApi.setBusy(false);

            });

        }
    }
})();

(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osCompareCluster', compareCluster);

    /** @ngInject */
    function compareCluster() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/compareclusters/compareclusters.html',
            controller: CompareClusterController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function CompareClusterController(osApi, osCohortService, d3, $state, $timeout, $scope, moment, $stateParams, _, $, $q, $window) {


            // Properties
            var vm = this;
            var table;
            

            // Retrieve Selected Patient Ids From OS Service
            var pc = osCohortService.getPatientCohort();

            if (pc==null){
                osCohortService.setPatientCohort([],"All Patients")
            }
            var selectedIds = (pc==null) ? [] : pc.ids;

            d3.ns.prefix.custom = "https://d3js.org/namespace/custom";

            osApi.getLayout()
            var width = 1000;
            var height = 1000;
            var sketch = d3.select("#compareclusterChart").append("custom:sketch")
                .attr("width", width)
                .attr("height", height)
                .call(custom);


        // On each mouse move, create a circle that increases in size and fades away.
        d3.select(window).on("mousemove", function() {
          sketch.append("custom:circle")
              .attr("x", d3.event.clientX)
              .attr("y", d3.event.clientY)
              .attr("radius", 0)
              .attr("strokeStyle", "red")
            .transition()
              .duration(5000)
              .ease(Math.sqrt)
              .attr("radius", 200)
              .attr("strokeStyle", "white")
              .remove();
        });

        function custom(selection) {
          selection.each(function() {
            var root = this,
                canvas = root.parentNode.appendChild(document.createElement("canvas")),
                context = canvas.getContext("2d");

            canvas.style.position = "absolute";
            canvas.style.top = root.offsetTop + "px";
            canvas.style.left = root.offsetLeft + "px";

            // It'd be nice to use DOM Mutation Events here instead.
            // However, they appear to arrive irregularly, causing choppy animation.
            d3.timer(redraw);

            // Clear the canvas and then iterate over child elements.
            function redraw() {
              canvas.width = root.getAttribute("width");
              canvas.height = root.getAttribute("height");
              for (var child = root.firstChild; child; child = child.nextSibling) draw(child);
            }

            // For now we only support circles with strokeStyle.
            // But you should imagine extending this to arbitrary shapes and groups!
            function draw(element) {
              switch (element.tagName) {
                case "circle": {
                  context.strokeStyle = element.getAttribute("strokeStyle");
                  context.beginPath();
                  context.arc(element.getAttribute("x"), element.getAttribute("y"), element.getAttribute("radius"), 0, 2 * Math.PI);
                  context.stroke();
                  break;
                }
              }
            }
          });
        };

            // // Load Datasets
            // osApi.setBusy(true);
            // osApi.query(vm.datasource.collections.patient, {
            //         $fields: fields
            //     })
            //     .then(function(response) {
            //         initDataTable(vm, columns, response.data);
            //         initEvents(vm, $scope, osApi)
            //         osApi.setBusy(false);
            //     });
        }
    }
})();

(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osColorPanel', colorPanel);

    /** @ngInject */
    function colorPanel() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/colorpanel/colorpanel.html',
            controller: ColorPanelController,
            controllerAs: 'vm',
            bindToController: true,
            scope:{
                close: "&"
            }
        };

        return directive;

        /** @ngInject */
        function ColorPanelController(osApi, osCohortService) {

            // Properties
            var vm = this;
            vm.showPanelColorRna = false;
            var table;

            var tbl = osApi.getDataSource().category.filter(function(v){ return v.type=='color'; })[0].collection;

            osApi.query(tbl, {
                type: 'color',
                dataset: osApi.getDataSource().disease,
                $fields: ['name','subtype']
            }).then(function(v) {

                var data = v.data.reduce(function(p,c){
                    if (!p.hasOwnProperty(c.subtype)) p[c.subtype] = [];
                    p[c.subtype].push(c);
                    return p;
                },{});

                vm.optPatientColors = Object.keys(data).map(function(key){
                    return {name:key, values:this[key].sort(function(a,b){
                        if (a.name>b.name) return 1;
                        if (a.name<b.name) return -1;
                        return 0;
                    })};
                }, data);
                // vm.optPatientColors = [{
                //     name: 'None'
                // }].concat(v.data);


            });

            vm.setColor = function(item) {

                if (item.name=="None"){
                    osCohortService.setPatientColor({"dataset":osApi.getDataSource().disease,"type":"color","name":"None","data":[], show:true})
                    return;
                }

                osApi.query(tbl, {
                    type: 'color',
                    dataset: osApi.getDataSource().disease,
                    name: item.name
                }).then(function(v) {
                    var data = v.data[0];
                    data.data = data.data.map(function(v){
                        var name = v.name.toLowerCase().trim();
                        if (name=="" || name=="null" || name=="undefined"){
                            v.name = "Null";
                            v.color = "#DDDDDD";
                        }
                        v.id = "legend-"+v.color.substr(1);
                        return v;
                    }).sort(function(a,b){
                        var aname = (parseInt(a.name)!=NaN) ? parseInt(a.name) : a.name;
                        var bname = (parseInt(b.name)!=NaN) ? parseInt(b.name) : b.name;
                        if (aname < bname) return -1;
                        if (aname > bname) return 1;
                        if (a.name=="Null") return 1;
                        if (b.name=="Null") return -1;
                        return 0;
                    })

                    // debugger;
                    osCohortService.setPatientColor(v.data[0]);
                    vm.close();
                });
            };

            vm.setGeneColor = function() {
                var genes = ("+" + vm.geneColor.replace(/\s/g, '').toUpperCase()).match(/[-+]\w*/gi).map(function(v) {
                    return {
                        op: v.substr(0, 1),
                        gene: v.substr(1)
                    };
                });
                osApi.query(osApi.getDataSource().rna, {
                    gene: {
                        '$in': genes.map(function(v) {
                            return v.gene;
                        })
                    }
                }).then(function(results) {

                    if (results.data.length > 0) {
                        var data;
                        if (results.data.length == 1)
                            data = results.data[0];
                        else {
                            data = {};
                            data.patients = results.data.reduce(function(p, c) {
                                var fn = p.lookup[c.gene];
                                for (var i = 0; i < p.pids.length; i++) {
                                    var pid = p.pids[i];
                                    var iv = p.output.hasOwnProperty(pid) ? p.output[pid] : 0;
                                    if (fn === "+") p.output[pid] = iv + c.patients[pid];
                                    if (fn === "-") p.output[pid] = iv - c.patients[pid];
                                }
                                return p;
                            }, {
                                pids: Object.keys(results.data[0].patients),
                                lookup: genes.reduce(function(p, c) {
                                    p[c.gene] = c.op;
                                    return p;
                                }, {}),
                                output: {}
                            }).output;

                            var range = Object.keys(data.patients).reduce(function(p, c) {
                                p.min = Math.min(p.min, p.values[c]);
                                p.max = Math.max(p.min, p.values[c]);
                                return p;
                            }, {
                                values: data.patients,
                                min: Infinity,
                                max: -Infinity
                            });
                            data.min = range.min;
                            data.max = range.max;
                        }

                        // Color Patients


                        var values = [{
                            name: 'Q1',
                            color: "#6a32b3"
                        }, {
                            name: 'Q2',
                            color: "#cd2c8e"
                        }, {
                            name: 'Q3',
                            color: "#e85434"
                        }, {
                            name: 'Q4',
                            color: "#a9a10a"
                        }];

                        var scale = d3.scale.quantize()
                            .domain([data.min, data.max])
                            .range(values.map(function(f) {
                                return f.color;
                            }));

                        data = Object.keys(data.patients).map(function(id) {
                                return {
                                    id: id,
                                    color: this.scale(this.patients[id])
                                };
                            }, {
                                patients: data.patients,
                                scale: scale
                            })
                            .reduce(function(p, c) {
                                if (!p.hasOwnProperty(c.color)) p[c.color] = [];
                                p[c.color].push(c.id);
                                return p;
                            }, {});

                        data = Object.keys(data).map(function(key) {
                            return {
                                color: key,
                                name: this.names.filter(function(f) {
                                    return f.color == this.color
                                }, {
                                    color: key
                                })[0].name,
                                values: this.data[key]
                            };
                        }, {
                            data: data,
                            names: values
                        })

                        data = data.sort(function(a,b){
                            if(a.name < b.name) return -1;
                            if(a.name > b.name) return 1;
                            return 0;
                        });
                        data.push({
                            color:'#DDD',
                            name: 'Null',
                            values: []
                        })

                        var colors = {
                            dataset: osApi.getDataSource().disease,
                            type: 'color',
                            name: genes.reduce(function(p, c) {
                                p += c.op + c.gene + " ";
                                return p;
                            }, ""),
                            data: data
                        };
                        osCohortService.setPatientColor(colors);
                    }

                });

            }
        }
    }
})();

(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osCohortMenu', cohortMenu);

    /** @ngInject */
    function cohortMenu() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/cohortmenu/cohortmenu.html',
            controller: CohortMenuController,
            controllerAs: 'vm',
            scope:{ },
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function CohortMenuController(osApi, osCohortService, $state, $scope, $timeout, $rootScope) {

            var vm = this;
            vm.cohorts = [];
            vm.patientChartOption = null;
            vm.addCohort = function(){};
            vm.setCohort = function(){};
            vm.removeCohort = function(){};
            vm.editItem = {name:''};
            vm.editCohort = function(item){
                vm.editItem = item;
                vm.edit = true;
            };
            
            vm.show = false;
            vm.edit = false;
            
            
            osCohortService.onCohortsChange.add(function(allCohorts){
                vm.cohorts = allCohorts;
                vm.show = true;
                vm.showPatientHistory();
                osCohortService.setPatientCohort([],"All Patients")
            });

            $rootScope.$on('$stateChangeStart', function(event, toState, toParams, fromState, fromParams){ 
                switch(toState.name){
                    case "landing":
                    case "tools":
                    case "datasource":
                        vm.show = false;
                        break;
                    default:
                        vm.show = true;
                        break;
                }
            });

            // Configure Tray
            var elTray = angular.element(".cohort-menu");
            var mouseOver = function(){
                elTray
                    .removeClass("tray-collapsed-left");
            }
            var mouseOut = function(){
                elTray
                    .addClass("tray-collapsed-left");
            }
            
            // Configure Tabs
            var elTabPatients = angular.element('#cohort-tab-patients');
            var elTabGenes    = angular.element('#cohort-tab-genes');
            vm.showPatientHistory = function(){
                elTabPatients.addClass("active");
                elTabGenes.removeClass("active");
                vm.cohorts = osCohortService.getPatientCohorts();
                vm.addCohort = osCohortService.addPatientCohort;
                vm.setCohort = osCohortService.setPatientCohort;
                vm.removeCohort = osCohortService.delPatientCohort;
            };
            vm.showGeneHistory = function(){
                elTabGenes.addClass("active");
                elTabPatients.removeClass("active");
                vm.cohorts = osCohortService.getGeneCohorts();
                vm.addCohort = osCohortService.addGeneCohort;
                vm.setCohort = osCohortService.setGeneCohort;
                vm.removeCohort = osCohortService.delGeneCohort;
            };

            var isLocked = true;
            vm.toggle = function(){
                isLocked = !isLocked;
                angular.element("#cohortmenu-lock")
                    .addClass(isLocked ? 'fa-lock' : 'fa-unlock-alt')
                    .removeClass(isLocked ? 'fa-unlock-alt' : 'fa-lock')
                    .attr("locked", isLocked ? "true" : "false");

                if (isLocked) {

                    elTray
                        .unbind("mouseover", mouseOver)
                        .unbind("mouseout", mouseOut)
                        .removeClass("tray-collapsed-left");
                    
                        
                } else {
                    elTray
                        .addClass("tray-collapsed-left")
                        .bind("mouseover", mouseOver)
                        .bind("mouseout", mouseOut);
                   
                }
                osApi.onResize.dispatch();
                    
            }   


            var barClick =function(d,i){
                
                
                if (vm.patientChartOption.type=="numeric"){
                    var bounds = d.label.split("-").map(function(v){ return parseInt(v); });
                    var prop = vm.patientChartOption.prop;
                    osCohortService.filterActivePatientCohort(bounds, prop, vm.patientChartOption.type);
                }
            }


            // Init SVG;
            var svg = d3.select("#cohortmenu-chart").append("svg")
                .attr("width", 238)
                .attr("height", 150)
                .append("g");
          
            $scope.$watch('vm.patientChartOption', function(){

                if (vm.patientChartOption==null) return;

                var data = vm.patientChartOption.data;

                var barWidth = Math.floor(238/data.bins);
                if (data.histRange[0]>0) data.histRange[0] -=2;

                var yScale = d3.scaleLinear()
                    .domain(data.histRange)
                    .range([0,135]);

                var bars = svg
                    .selectAll(".cohort-menu-chart-bar")
                    .data(data.hist);

                    bars.enter()
                        .append("rect")
                        .attr("class","cohort-menu-chart-bar")
                        .attr("x", function(d, i) { return (barWidth+1) * i; })
                        .attr("y", function(d, i) { return 150-yScale(d.value); })
                        .attr("height", function(d, i) { return yScale(d.value); })
                        .attr("width", barWidth)
                        .on("click", barClick);

                    bars
                        .transition()
                            .duration(300)
                            .attr("x", function(d, i) { return (barWidth+1) * i; })
                            .attr("y", function(d, i) { return 150-yScale(d.value); })
                            .attr("height", function(d, i) { return yScale(d.value); })
                            .attr("width", barWidth)

                    bars.exit()
                        .transition()
                            .duration(300)
                            .attr("y", 150)
                            .attr("height", 0)
                            .style('fill-opacity', 1e-6)
                            .remove();

                    var labels = svg
                        .selectAll("text")
                        .data(data.hist)

                    labels.enter()
                        .append("text")
                        .attr("x", function(d, i) { return ((barWidth+1) * i) + (barWidth*.5); })
                        .attr("y", function(d, i) { 
                            return 145-yScale(d.value);
                        })
                        .attr("fill", "#000")
                        .attr("height", function(d, i) { return yScale(d.value); })
                        .attr("width", barWidth)
                        .attr("font-size", "8px")
                        .attr("text-anchor", "middle")
                        .text(function(d){ return d.label; });

                    labels
                        .transition()
                            .duration(300)
                            .attr("x", function(d, i) { return ((barWidth+1) * i) + (barWidth*.5); })
                            .attr("y", function(d, i) { 
                                var y = 145-yScale(d.value);
                                if (y<0) y = 20;
                                return y;
                            })
                            .text(function(d){ return d.label; });

                    labels.exit()
                        .transition()
                            .duration(300)
                            .attr("y", 150)
                            .attr("height", 0)
                            .style('fill-opacity', 1e-6)
                            .remove();
                            

            });


            // Interact with Cohort Service
            osCohortService.onPatientsSelect.add(function(obj){
                osCohortService.getPatientMetric();
            });
            osCohortService.onGenesSelect.add(function(obj){
                console.log("GENES");
            });
            osCohortService.onMessage.add(function(obj){
                if (obj.data.cmd!="setPatientMetric") return;
                $timeout(function(){
                    vm.patientTotal = obj.data.data.total,
                    vm.patientSelected = obj.data.data.selected,
                    vm.patientChartOptions = obj.data.data.features;
                    vm.patientChartOption = vm.patientChartOptions[0];
                });                
            });            

            // And Go
            vm.showPatientHistory();
            
        }
    }

})();

 (function() {
    'use strict';

    angular
        .module('oncoscape')
        .run(runBlock);

    /** @ngInject */
    function runBlock($rootScope, $state, $window, $exceptionHandler, osApi, osAuth) { //, $log

        // Route Errors To Angular
        $window.onerror = function handleGlobalError( message, fileName, lineNumber, columnNumber, error ) {
            if ( ! error ) {
                error = new Error( message );
                error.fileName = fileName;
                error.lineNumber = lineNumber;
                error.columnNumber = ( columnNumber || 0 );
            }
            $exceptionHandler( error );
        }
        
        // Actions To Take On State Change
        var off = $rootScope.$on('$stateChangeStart', function(event, toState, toParams) {

            // Hide Busy Cursor
            osApi.setBusy(false);

            // Reset DataTable Custom Filters
            angular.element.fn.DataTable.ext.search = [];

            // Route unauthenticated users to landing page
            if (toState.authenticate && !osAuth.isAuthenticated()) {
                $state.transitionTo("landing");
                event.preventDefault();
                return;
            }

            //Redirect If Unable To Resolve Data Source
            if (toState.datasource && (angular.isUndefined(toParams.datasource) || toParams.datasource==="")){
                $state.transitionTo("datasource")
                event.preventDefault();
                return;
            }else{
                osApi.setDataSource(toParams.datasource);
            }            

            //osApi.setDataSource({"disease":"brca","source":"TCGA","name":"Breast","img":"DSbreast.png","beta":false,"collections":{"drug":"clinical_tcga_brca_drug","f1":"clinical_tcga_brca_f1","f2":"clinical_tcga_brca_f2","f3":"clinical_tcga_brca_f3","nte":"clinical_tcga_brca_nte","nte_f1":"clinical_tcga_brca_nte_f1","omf":"clinical_tcga_brca_omf","pt":"clinical_tcga_brca_pt","rad":"clinical_tcga_brca_rad"},"edges":[{"name":"TCGA GBM Classifiers","edges":"edge_brca_tcgagbmclassifiers","patientWeights":"edge_brca_tcgagbmclassifiers_patient_weight","genesWeights":"edge_brca_tcgagbmclassifiers_gene_weight"},{"name":"Marker Genes 545","edges":"edge_brca_markergenes545","patientWeights":"edge_brca_markergenes545_patient_weight","genesWeights":"edge_brca_markergenes545_gene_weight"},{"name":"TCGA Pan Cancer Mutated","edges":"edge_brca_tcgapancancermutated","patientWeights":"edge_brca_tcgapancancermutated_patient_weight","genesWeights":"edge_brca_tcgapancancermutated_gene_weight"},{"name":"Onco Vogel 274","edges":"edge_brca_oncovogel274","patientWeights":"edge_brca_oncovogel274_patient_weight","genesWeights":"edge_brca_oncovogel274_gene_weight"}]});
        });

        $rootScope.$on('$destroy', off)
    }
})();
